import React, { Component } from 'react';
import { Table } from 'antd';
import './view6.css';

export default class View6 extends Component {
  selectUser = (user) => {
    this.props.changeSelectUser(user);
  };

  render() {
    const { data } = this.props;

    const columns = [
      {
        title: 'Malware Family',
        dataIndex: 'malwareFamily',
        key: 'malwareFamily',
      },
      {
        title: 'IP',
        dataIndex: 'ip',
        key: 'ip',
      },
      {
        title: 'Date',
        dataIndex: 'date',
        key: 'date',
      },
      {
        title: 'AS',
        dataIndex: 'as',
        key: 'as',
      },
      {
        title: 'Country',
        dataIndex: 'country',
        key: 'country',
      },
      {
        title: 'Source',
        dataIndex: 'source',
        key: 'source',
      },
    ];

    return (
      <div id="view6" className="pane">
        <div className="header">C2 Servers</div>
        <Table
          size="small"
          bordered
          dataSource={data}
          columns={columns}
          rowKey={(record, index) => index}
        />
      </div>
    );
  }
}
