import React, { Component } from 'react';
import data from './data';
import { Layout } from 'antd';
import View1 from './views/View1';
import View2 from './views/View2';
import View3 from './views/View3';
import View4 from './views/View4';
import View5 from './views/View5';
import View6 from './views/View6';
import './dashboard.css';
import View7 from './views/View7';
import View8 from './views/View8';


const { Sider, Content, Footer } = Layout;

export default class Dashboard extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedUser : {"Name": "Guest", "age": "30", "gender": "male"},
            data : data,
            country : "ALL",
            malwareFamily : "ALL",
            fromDate : "2020-01-01",
            toDate : new Date().toLocaleDateString('en-CA'),
            as : "ALL",
            source : "ALL",
            plotType : "Geo Location",
            
        }
    }

    handleSubmit = (options) => {
    console.log(options);
        this.setState({
            country : options.country,
            malwareFamily : options.malwareFamily,
            fromDate : options.fromDate,
            toDate : options.toDate,
            as : options.as,
            source : options.source,
            plotType : options.plotType,
        })
    };


    render() {
        const {selectedUser, data, country, malwareFamily, fromDate, toDate, as, source, plotType} = this.state;
        var fData = data
        console.log(this.state)
        if(malwareFamily && !malwareFamily.includes("ALL")){
            fData = fData.filter(item => malwareFamily.includes(item.malwareFamily));
        }

        if(country && !country.includes("ALL")){
            fData = fData.filter(item => country.includes(item.country));
        }

        if(as && !as.includes("ALL")){
            fData = fData.filter(item => as.includes(item.as));
        }

        if(source && !source.includes("ALL")){
            fData = fData.filter(item => source.includes(item.source));
        }

        var fDate = fromDate;
        var tDate = toDate;
        if(!fDate){
            fDate = "2020-01-01";
        }
        if(!tDate){
            tDate = new Date().toLocaleDateString('en-CA');
        }

        fData = fData.filter(item => {
          const itemDate = new Date(item.date);
          const startRange = new Date(fDate);
          const endRange = new Date(tDate);
          return itemDate >= startRange && itemDate <= endRange;
        });

        return (

            <div>
                <div> 
                <Content style={{  height: 40, justifyContent: 'center', alignItems: 'center' }}>
                            <View8
                            />
                        </Content>
                </div>
                <Layout style={{ height: 920 }}>
                    <Sider width={300} style={{backgroundColor:'#eee'}}>
                        <Content style={{ height: 200 }}>
                            <View1 user={selectedUser}/>
                        </Content>
                        <Content style={{ height: 1000 }}>
                            <View3 
                                onSubmit={this.handleSubmit}
                            />
                        </Content>
                    </Sider>
                    <Layout>
                        <Layout style={{ height: 900 }}>
                        <Content style={{ height: 700 }}>
                            <View7 data={fData} />
                        </Content>
                        </Layout>
                        <Layout style={{ height: 450 }}>
                            <Sider width={1525} style={{backgroundColor:'#eee'}}>
                                <View6 data={fData} changeSelectUser={this.changeSelectUser}/>
                            </Sider>
                        </Layout>
                    </Layout>
                </Layout>
            </div>
        )
    }
}
