import * as d3 from 'd3';

const draw = (props) => {

  const data  = props.data;
  var countByCountry = {};

  data.forEach(function(d) {
    var country = d.country;
    countByCountry[country] = (countByCountry[country] || 0) + 1;
  });
  
  console.log(countByCountry);
  var scoresByCountry = countByCountry 
  var colorScale = d3.scaleLinear()
    .domain([0, d3.max(Object.values(scoresByCountry))+5])
    .range(["lavender", "darkblue"]);

  d3.select('.vis-mapchart > *').remove();
  let margin = { top: 20, right: 20, bottom: 30, left: 40 };
  const width = props.width - margin.left - margin.right;
  const height = props.height - margin.top - margin.bottom;
  const svg = d3.select('.vis-mapchart')
    .append('svg')
    .attr('width', width)
    .attr('height', height);

  const projection = d3.geoNaturalEarth1()
    .scale(width / 2 / Math.PI)
    .translate([width / 2, height / 2]);

  const path = d3.geoPath().projection(projection);

  d3.json('https://raw.githubusercontent.com/nvkelso/natural-earth-vector/master/geojson/ne_110m_admin_0_countries.geojson')
    .then(function (geojson) {
      if(Object.keys(countByCountry).length !== 0){
        svg.selectAll('path')
        .data(geojson.features)
        .enter()
        .append('path')
        .attr('d', path)
        .style('fill', 'lavender')
        .style("fill", function(d) {
        var country = d.properties.ADMIN;
        var score = scoresByCountry[country] || 0;
        return colorScale(score);
      });
      }
      else{
        svg.selectAll('path')
        .data(geojson.features)
        .enter()
        .append('path')
        .attr('d', path)
        .style('fill', 'lavender');
      }


      const legendWidth = 20;
      const legendHeight = height - 120;
      const legendX = width - 80;
      const legendY = 20;

      const legendScale = d3.scaleLinear()
        .domain([0, d3.max(Object.values(scoresByCountry))])
        .range([legendHeight, 0]);

      const gradient = svg.append("defs")
        .append("linearGradient")
        .attr("id", "legendGradient")
        .attr("x1", 0)
        .attr("y1", 0)
        .attr("x2", 0)
        .attr("y2", 1);

         gradient.append("stop")
          .attr("offset", "0%")
          .attr("stop-color", colorScale(d3.max(Object.values(scoresByCountry))));

        gradient.append("stop")
          .attr("offset", "100%")
          .attr("stop-color", colorScale(0));

      svg.append("rect")
        .attr("class", "legend-gradient")
        .attr("x", legendX)
        .attr("y", legendY)
        .attr("width", legendWidth)
        .attr("height", legendHeight)
        .attr("fill", "url(#legendGradient)");

      svg.append("text")
        .attr("class", "legend-label")
        .attr("x", legendX + 15)
        .attr("y", legendY + legendHeight + 15)
        .style("font-size", "12px")
        .style("fill", "black");

      const legendTicks = legendScale.ticks(5);

      svg.selectAll(".legend-tick")
        .data(legendTicks)
        .enter()
        .append("line")
        .attr("class", "legend-tick")
        .attr("x1", legendX + legendWidth + 5)
        .attr("y1", (d) => legendY + legendScale(d))
        .attr("x2", legendX + legendWidth + 15)
        .attr("y2", (d) => legendY + legendScale(d))
        .style("stroke", "black")
        .style("stroke-width", "1px");

      svg.selectAll(".legend-tick-label")
        .data(legendTicks)
        .enter()
        .append("text")
        .attr("class", "legend-tick-label")
        .attr("x", legendX + legendWidth + 20)
        .attr("y", (d) => legendY + legendScale(d) + 4)
        .text((d) => d)
        .style("font-size", "10px")
        .style("fill", "black")
        .attr("alignment-baseline", "middle");


    })
    .catch(function (error) {
      console.log('Error loading GeoJSON data:', error);
    });
};

export default draw;
